





























import { Component } from 'vue-property-decorator'
import BaseComponent from '../../core/BaseComponent'
import { BORDER_STYLE } from '../../constants/Style'

@Component
export default class BorderStyleComponent extends BaseComponent {
  toggle = false
  borderWidth = '1'
  borderStyle = 'solid'
  borderColor = '#ffffff'

  get widthOptions() {
    const lists = []
    for (let i = 1; i < 16; i++) {
      lists.push({ label: `${i}px`, value: `${i}px` })
    }
    return lists
  }

  get borderStyles() {
    return BORDER_STYLE
  }

  doGetData(value: any) {
    this.toggle = value
    if (this.toggle) {
      this.onEmitData()
    }
  }

  onUpdateWidth(width: any) {
    this.borderWidth = width
    this.onEmitData()
  }

  onUpdateStyle(style: any) {
    this.borderStyle = style
    this.onEmitData()
  }

  onUpdateColor(color: any) {
    this.borderColor = color
    this.onEmitData()
  }

  onEmitData() {
    this.$emit('click', {
      'border-bottom': {
        width: `${this.borderWidth}px`,
        style: this.borderStyle,
        color: this.borderColor
      }
    })
  }
}
